
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a {
  color: #000;
  text-decoration: none;
}
html {
  height: 100%;
}
body {
  font-size: 14px;
  font-family: '微软雅黑';
}
ul,li {
  list-style: none;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



